
import axios from "axios";

import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Previewer,
ViewBox,
XButton
},

methods: {
getList1() {
axios
.get("pscoreparty/showDakDetialByUserId", {
params: {
userId: this.$route.params.userId
}
})
.then(res => {
let jsonObj = JSON.parse(res.data.remark);
this.content1 = jsonObj.title;
this.content2 = jsonObj.remark;
this.adderName = res.data.adderName;

if (res.data.imgs) {
var imgs = res.data.imgs.split(",");
this.imgpics = [];
for (var i = 0; i < imgs.length; i++) {
var obj = {};
obj.msrc =
"http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId=" +
imgs[i];
obj.src =
"http://www.dlbdata.cn/dangjian/picture/show?pictureId=" +
imgs[i];
this.imgpics.push(obj);
}
} else {
this.imgpics = [];
}
})
.catch(err => {
console.log(err);
});
},
getUser1() {
axios
.get("ppartymember/queryByUserId", {
params: {
userid: this.$route.params.userId
}
})
.then(res => {
this.userId = res.data.userid;
this.departmentid = res.data.departmentid;
this.name = res.data.name;
this.departmentname = res.data.departmentname;
})
.catch(err => {
console.log(err);
});
},
show(index) {
this.$refs.previewer.show(index);
},

spread() {
this.spr = true;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
folding() {
this.spr = !this.spr;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
noSpread() {
this.noSpr = true;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
noFolding() {
this.noSpr = !this.noSpr;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
logIndexChange(arg) {
console.log(arg);
}
},
mounted() {
this.getUser1();
this.getList1();
},
data() {
return {
num: 0,
userId: "",
adderName: "",
name: "",
content1: "",
content2: "",
departmentname: "",
activeData: {},
picInfo: [],
list: [],
content: {},
imgpics: [],
imgs: "",
spr: false,
noSpr: false,
nobtnPack: false,
nobtnAn: false,
btnPack: false,
btnAn: false,
peopleNum: null,
participants: null,
Noparticipants: null,
options: {
getThumbBoundsFn(index) {

let thumbnail = document.querySelectorAll(".previewer-demo-img")[
index
];

let pageYScroll =
window.pageYOffset || document.documentElement.scrollTop;


let rect = thumbnail.getBoundingClientRect();

return { x: rect.left, y: rect.top + pageYScroll, w: rect.width };


}
}
};
}
};



